// Side Rails
export const getFabricSideRailsException = (state) => {
    if (state.juniorKit) {
        return "Not available with Junior Kit";
    }
    return null;
};
// Safety
export const getBumpersException = (state) => {
    if (state.juniorKit) {
        return "Included with Junior Kit";
    }
    return null;
};
export const getSafetySleeveException = (state) => {
    if (state.juniorKit) {
        return "Included with Junior Kit";
    }
    else if (state.lever !== "None" || state.liftingPole) {
        return "Not available with mobility options";
    }
    return null;
};
export const getJuniorKitException = (state) => {
    if (state.sideRails === "Fabric") {
        return "Not available with Fabric Side Rails";
    }
    return null;
};
// Mobility
export const getMobilityOptionsException = (state) => {
    if (state.safetySleeve) {
        return "Not available with Safety Sleeve";
    }
    return null;
};
// Extras
export const getPumpHolderException = (state) => {
    if (state.bumpers) {
        return "Not available with Head and Footboard Bumpers";
    }
    return null;
};
export const getBedExtensionException = (state) => {
    if (state.bedExtensionKit) {
        return "Included with Bed Extension Kit";
    }
    else if (state.safetySleeve) {
        return "Not available with Safety Sleeve";
    }
    return null;
};
export const getMattressExtensionFoamException = (state) => {
    if (state.bedExtensionKit) {
        return "Included with Bed Extension Kit";
    }
    else if (state.safetySleeve) {
        return "Not available with Safety Sleeve";
    }
    return null;
};
export const getBedExtensionKitException = (state) => {
    if (state.safetySleeve) {
        return "Not available with Safety Sleeve";
    }
    return null;
};
